import {Component, ElementRef, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {Router} from '@angular/router';
import {ModalPopupPage} from '../../shared-components/modal-popup/modal-popup.page';
import {IonSlides, ModalController} from '@ionic/angular';
import {ApiService} from '../../api.service';
import { Observable, interval } from 'rxjs';
import {SharedServiceService} from '../../shared-service.service';
@Component({
  selector: 'app-feed',
  templateUrl: './feed.page.html',
  styleUrls: ['./feed.page.scss'],
})
export class FeedPage implements OnInit {
  slideOpts = {
    initialSlide: 1,
    speed: 400,
    autoplay: true,
    zoom: {
      maxRatio: 3,
    },
    loop: true,
  };
  productImages = [
    {
      name: 'Product 1',
      url: 'assets/product1.jpg',
      price: 345,
      discountedPrice: 355,
      inCart: false,
      addedInCart: 0
    },
    {
      name: 'Product 2',
      url: 'assets/product2.jpeg',
      price: 45,
      discountedPrice: 60,
      inCart: false,
      addedInCart: 0
    },
    {
      name: 'Product 3',
      url: 'assets/product3.jpeg',
      price: 126,
      discountedPrice: 150,
      inCart: false,
      addedInCart: 0
    },
    {
      name: 'Product 4',
      url: 'assets/product4.jpeg',
      price: 324,
      discountedPrice: 387,
      inCart: false,
      addedInCart: 0
    },
  ];
  packagesOption = {
    slidesPerView: 1.5,
    centeredSlides: true,
    loop: true,
    initialSlide: 2,
    spaceBetween: 10,
    speed: 400,
    autoplay: true,
  };
  treatments = [
    {
      name: 'Threading',
      sequence: 1,
      url: 'assets/vector-icons/eyebrow.png',
    },
    {
      name: 'waxing',
      sequence: 2,
      url: 'assets/vector-icons/waxing.png',
    },
    {
      name: 'Haircuts',
      sequence: 3,
      url: 'assets/vector-icons/haircuts.png',
    },
    {
      name: 'Facials',
      sequence: 4,
      url: 'assets/vector-icons/facial.png',
    },
    {
      name: 'Bleach',
      sequence: 5,
      url: 'assets/vector-icons/bleach.png',
    },
    {
      name: 'Hairs',
      sequence: 6,
      url: 'assets/vector-icons/hair-straightener.png',
    },
    {
      name: 'Body',
      sequence: 7,
      url: 'assets/vector-icons/body-treatments.png',
    },
    {
      name: 'Makeup',
      sequence: 8,
      url: 'assets/vector-icons/makeup.png',
    },
    {
      name: 'Courses',
      sequence: 9,
      url: 'assets/vector-icons/badge.png',
    }
  ];
  packages = [
    {
      id: 1,
      sequence: 1,
      url: 'assets/s1.jpg',
    },
    {
      id: 2,
      sequence: 2,
      url: 'assets/s2.jpg',
    },
    {
      id: 3,
      sequence: 3,
      url: 'assets/s3.jpg',
    },
    {
      id: 4,
      sequence: 4,
      url: 'assets/s4.jpeg',
    }
  ];
  packageList = [];
  productList = [];
  serviceTypesList = [];
  todayYear: any;
  bannerAnim1: any = false;
  changeAnim1: any = false;
  changeAnim2: any = false;
  packagesSliderObservable: any = new Observable();
  bannerSliderObservable: any = new Observable();
  @ViewChild('packagesSlider', { static: false }) packagesSlider: IonSlides;
  @ViewChild('bannerSlider', { static: false }) bannerSlider: IonSlides;
  @ViewChild('animationRef', { read: ElementRef, static: false }) animationRef: ElementRef;
  constructor(private sharedService: SharedServiceService, private router: Router, private adminService: ApiService) { }

  ionViewDidEnter() {
    this.packagesSliderObservable = interval(3000).subscribe(x => {
      this.packagesSlider.startAutoplay().then();
    });
    this.bannerSliderObservable = interval(3000).subscribe(x => {
      this.bannerSlider.startAutoplay().then();
    });
  }

  onIonScroll(event) {
    // const eventDetails = event.detail;
    // const slideUpCssList = ['animate__animated', 'animate__slideInUp', 'animate__fast'];
    // switch (eventDetails.scrollTop) {
    //   case 200: {
    //     this.changeAnim1 = false;
    //     setTimeout(() => {
    //       this.changeAnim1 = true;
    //     }, 20);
    //     break;
    //   } case 600: {
    //     this.changeAnim2 = false;
    //     setTimeout(() => {
    //       this.changeAnim2 = true;
    //     }, 20);
    //     break;
    //   }
    // }
  }

  ionViewDidLeave() {
    this.packagesSliderObservable.unsubscribe();
    this.bannerSliderObservable.unsubscribe();
  }

  slidesDidLoad(event) {
    setTimeout(() => {
      this.packagesSlider.startAutoplay().then();
      this.bannerSlider.startAutoplay().then();
    }, 1000);
  }


  onLastBannerSlide() {
    this.bannerSlider.slideTo(2).then();
  }

  onLastPackageSlide() {
    this.packagesSlider.slideTo(2).then();
  }

  ngOnInit() {
    this.getPackages();
    this.getProducts();
    this.getServiceTypes();
    this.todayYear = (new Date()).getFullYear();
  }


  refreshPage(event) {
    setTimeout(() => {
      this.getPackages();
      this.getProducts();
      event.target.complete();
    }, 1000);
  }

  getPackages() {
    this.sharedService.showSpinner.next(true);
    this.adminService.getServicePackage().subscribe(
        res => this.getServicePackageSuccess(res),
        error => {
          this.adminService.commonError(error);
        }
    );
  }

  getServicePackageSuccess(res) {
    this.packageList = res;
    this.sharedService.showSpinner.next(false);
  }

  getProducts() {
    this.sharedService.showSpinner.next(true);
    this.adminService.getProducts().subscribe(
        res => this.getProductSuccess(res),
        error => {
          this.adminService.commonError(error);
        }
    );
  }

  getProductSuccess(res) {
    this.productList = res;
    this.sharedService.showSpinner.next(false);
  }

  getServiceTypes() {
    this.sharedService.showSpinner.next(true);
    this.adminService.getAllServiceTypes().subscribe(
        res => this.getAllServiceTypesSuccess(res),
        error => {
          this.adminService.commonError(error);
        }
    );
  }

  getAllServiceTypesSuccess(res) {
    this.serviceTypesList = res;
    this.sharedService.showSpinner.next(false);
  }

  onClickPackage(id) {
    this.router.navigate(['/package-details'], { queryParams: { packageId: id }});
  }

  goServices() {
    this.router.navigate(['/services']);
  }

  onKnowMore() {
    this.router.navigate(['/about-us']);
  }

  onUpdateCounter(data, index) {
    this.productImages[index].addedInCart = data;
  }
}
